*{
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  border: none;
}

ul{
  list-style: none;
}

body{
  font-family: "Karla",sans-serif;
  background-color: #F5F7FB;
  background-image: url("data:image/png;base64,"), url("data:image/png;base64,");
  background-repeat: no-repeat, no-repeat;
  background-position: top right,bottom left;
  height: 100%;
}

.main{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.main .description{
  display: flex;
  flex-direction: column;
  gap: 1em;
  align-items: center;
  color: #293264;
}

.description h1{
  font-size: 31px;
  line-height: 37px;
}

.description p{
  font-weight: 400;
}

.btn{
  width: 193px;
  height: 52px;
  font-size: 16px;
  background-color: #4D5B9E;
  border-radius: 15px;
  color: white;
  font-family: "Karla";
  font-weight: 500;
  margin-top: 20px;
  cursor: pointer;
  transition: all .1s ease ;
}

.btn:active{
  box-shadow: inset 1px 1px 10px rgba(0, 0, 0, 0.6);
}

.questionContainer{
  display: flex;
  justify-content: center;
  padding: 3em 0em;
  color: #293264;
}

.questionPage{
  display: flex;
  flex-direction: column;
  max-width: 700px;
}

.check{
  align-self: center;
}

.question{
  padding-bottom: 20px;
  border-bottom: 1px solid #DBDEF0;
  margin-bottom: 20px;
}

.question h3{
  margin-bottom: 13px;
}

.question > ul{
  display: flex;
  gap: 13px;
}

.question li{
  border: 1px solid #4D5B9E;
  border-radius: 15px;
  padding: 4px 13px;
  text-align: left;
  cursor: pointer;
  transition: all .3s ease;
}

.question p{
  font-weight: 700;
}

.selected{
  background: #D6DBF5;
  font-weight: 500;
}

.correct{
  background-color: #94D7A2;
  font-weight: 500;
  color: #293264;
}

.incorrect{
  background-color: #F8BCBC;
  font-weight: 300;
  color: rgba(41, 50, 100, .8); 
}

.score{
  padding: 2em;
  align-self: center;   
}